/* Base Styles for Inspection Container */
.inspection-container {
  position: sticky;
  top: 20px; /* Adjust the top offset as needed */
  margin-left: auto;
  margin-right: auto;
  max-width: 1200px;
  padding: 15px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* Inspection Category Headers */
.inspection-category h3 {
  cursor: pointer;
  color: #5a5a5a;
  background-color: #e9ecef;
  padding: 12px 20px;
  margin: 5px 0;
  border-radius: 8px;
  font-weight: 500;
  transition: all 0.3s ease-in-out;
}

.inspection-category h3:hover,
.inspection-category h3.active {
  background-color: #007bff;
  color: #ffffff;
  transform: translateY(-2px);
  box-shadow: 0 2px 4px rgba(0,76,152,0.2);
}

/* Responsive Grid Layout for Inspection Items */
.inspection-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 20px;
  padding-top: 20px;
}

/* Style for Individual Inspection Items */
.inspection-item {
  border: 1px solid #e2e2e2;
  border-radius: 8px;
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  background-color: #f8f9fa;
}

.inspection-item:hover {
  transform: scale(1.03);
  box-shadow: 0 6px 12px rgba(0,0,0,0.1);
}

.inspection-item img,
.inspection-item iframe {
  max-width: 100%;
  height: auto;
  border-bottom: 1px solid #e2e2e2;
}

.inspection-item p {
  font-size: 0.95rem;
  color: #4a4a4a;
  padding: 10px;
  margin: 0;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .inspection-container {
    position: sticky;
    top: 20px; /* Adjust as needed */
    z-index: 100; /* Ensure the container is above other content */
  }
}
